<template>
  <div class="row">
    <template v-for="(node,index) in nodes">
      <div class="col-6" :key="index">
        <b-form @submit.prevent="updateOptions(node.name)">
          <b-card no-body class="overflow-hidden">
            <div class="row">
              <b-col md="8">
                <b-card-body :title="node.name">
                  <b-card-text>This range will be use to take the daily % randomly</b-card-text>

                  <label for="feedback-user">Starting range</label>
                  <b-input v-if="selectedNode === node.id" v-model="selectedNodeInit"></b-input>
                  <b-input v-else :value="setNodes.find(f => f.id == node.id)['init']" disabled></b-input>
                  <label for="feedback-user">Ending range</label>
                  <b-input v-if="selectedNode === node.id" v-model="selectedNodeEnd"></b-input>
                  <b-input v-else :value="setNodes.find(f => f.id == node.id)['end']" disabled></b-input>
                </b-card-body>
              </b-col>
              <b-col md="4" class="d-flex justify-content-center align-items-center w-100">
                <img class="w-50 rounded-0" src="/img/icons/technical-support.png" />
              </b-col>
            </div>
            <template v-slot:footer>
              <div class="row">
                <div class="col-6">
                  <button
                    @click="editNode(node)"
                    type="button"
                    class="btn btn-xs btn-outline-dark btn-block"
                  >Edit</button>
                </div>
                <div class="col-6">
                  <button
                    v-if="!loading"
                    type="submit"
                    class="btn btn-xs btn-warning btn-block 6"
                    :disabled="selectedNode !== node.id"
                  >Save</button>
                  <button v-else type="submit" class="btn btn-xs btn-block btn-warning" disabled>
                    <i class="fa fa-spinner fa-spin"></i>
                  </button>
                </div>
              </div>
            </template>
          </b-card>
        </b-form>
      </div>
    </template>
  </div>
</template>

<script>
import _ from "lodash";
import incentiveCalculator from "./Calculator";

export default {
  name: "configurations",
  data() {
    return {
      loading: false,
      selectedNode: null,
      selectedNodeInit: 0,
      selectedNodeEnd: 0,
      nodes: []
    };
  },
  components: {
    incentiveCalculator
  },
  created() {
    this.listOptions();
  },
  computed: {
    setNodes() {
      return this.nodes.map(f => {
        return {
          id: f.id,
          init: JSON.parse(f.value).starting,
          end: JSON.parse(f.value).ending
        };
      });
    }
  },
  watch: {
    incentive_range_init: function(newValue, oldValue) {
      this.model.value = JSON.stringify({
        starting: newValue,
        ending: this.incentive_range_end
      });
    }
  },
  methods: {
    editNode(node) {
      this.selectedNode = node.id;
      this.selectedNodeInit = this.setNodes.find(f => f.id == node.id)["init"];
      this.selectedNodeEnd = this.setNodes.find(f => f.id == node.id)["end"];
    },
    listOptions() {
      this.loading = true;
      this.$http
        .get("/api/v1/admin/system_options", {
          headers: {
            "content-type": "application/json",
            Authorization: this.$store.getters.authToken
          }
        })
        .then(response => {
          this.nodes = response.data;
          this.loading = false;
        })
        .catch(error => {
          console.log(error);
        });
    },
    updateOptions(name) {
      this.loading = true;
      let data = JSON.stringify({
        starting: this.selectedNodeInit,
        ending: this.selectedNodeEnd
      });
      console.log(data);
      this.$http({
        url: "/api/v1/admin/system_options/" + name,
        method: "put",
        headers: {
          "content-type": "application/json",
          Authorization: this.$store.getters.authToken
        },
        data: { value: data }
      })
        .then(response => {
          this.loading = false;
          this.selectedNode = null;
          this.listOptions();
          this.$toasted.show("Data Updated", {
            position: "top-right",
            duration: 5000,
            type: "success",
            closeOnSwipe: true
          });
        })
        .catch(() => {
          console.log(error);
        });
    }
  }
};
</script>
