<template>
  <div class="col-12 d-flex justify-content-center">
    <div class="col-8">
      <div class="card">
        <div class="card-header">
          Incentive Calculator
          <div class="i fa fa-calculator"></div>
        </div>
        <form action>
          <b-card-body>
            <label for="feedback-user">Daily Profit (%)</label>
            <b-input class="border-success" v-model="initialValue"></b-input>
            <div class="row mt-4">
              <div class="col-12">
                <hr />
                <div class="row">
                  <div class="col-6">
                    <label class="font-weight-bold">REGULAR ACCOUNTS (Starting range)</label>
                    <b-input
                      v-model="regularAccountsStart"
                      v-clipboard:copy="regularAccountsStart"
                      v-clipboard:success="onCopy"
                      :disabled="initialValue === 0 || initialValue === null || initialValue === ''"
                    />
                  </div>
                  <div class="col-6">
                    <label class="font-weight-bold">REGULAR ACCOUNTS (Ending range)</label>
                    <b-input
                      v-model="regularAccountsEnd"
                      v-clipboard:copy="regularAccountsEnd"
                      v-clipboard:success="onCopy"
                      :disabled="initialValue === 0 || initialValue === null || initialValue === ''"
                    ></b-input>
                  </div>
                </div>
              </div>

              <div class="col-12">
                <hr />
                <div class="row">
                  <div class="col-6">
                    <label class="font-weight-bold">PRO ACCOUNTS (Starting range)</label>
                    <b-input
                      v-model="proAccountsStart"
                      v-clipboard:copy="proAccountsStart"
                      v-clipboard:success="onCopy"
                      :disabled="initialValue === 0 || initialValue === null || initialValue === ''"
                      class="border-danger"
                    ></b-input>
                  </div>
                  <div class="col-6">
                    <label class="font-weight-bold">PRO ACCOUNTS (Ending range)</label>
                    <b-input
                      v-model="proAccountsStartEnd"
                      v-clipboard:copy="proAccountsStartEnd"
                      v-clipboard:success="onCopy"
                      :disabled="initialValue === 0 || initialValue === null || initialValue === ''"
                      class="border-danger"
                    ></b-input>
                  </div>
                </div>
              </div>
            </div>
          </b-card-body>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import VueClipboard from "vue-clipboard2";

Vue.use(VueClipboard);

export default {
  name: "incentive-calculator",
  data() {
    return {
      initialValue: 0,
      regularAccountsStart: 0,
      regularAccountsEnd: 0,
      proAccountsStart: 0,
      proAccountsStartEnd: 0,
    };
  },
  watch: {
    initialValue: function (newVal) {
      this.regularAccountsStart = (newVal * 0.75 * 0.995).toFixed(4);
      this.regularAccountsEnd = (newVal * 0.75).toFixed(4);
      this.proAccountsStart = (newVal * 0.0833 * 0.995).toFixed(4);
      this.proAccountsStartEnd = (newVal * 0.0833).toFixed(4);
    },
  },
  methods: {
    onCopy() {
      console.log("sss");
      this.$toasted.info("Copied to clipboard!", {
        position: "top-right",
        duration: 5000,
        closeOnSwipe: true,
      });
    },
  },
};
</script>